
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: grid;
  grid: [row1-start] "header header header" 1fr [row1-end] [row2-start] "main main main" [row2-end]/auto 50% auto;
}
* {
  padding: 0;
  margin: 0;
}

.navbar {
  grid-area: header;
}

.blue {
  color: #258def;
}
a, a:visited, a:hover, a:active {
  color: inherit;
}
.pd-b {
  padding-bottom: .5rem;
}
.pd-t {
  padding-top: 1rem;
}
.homepage {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  grid-area: main;
  color: white;
}
.homepage .fade-in {
    opacity: 1;
    -webkit-animation-name: fadeInOpacity;
            animation-name: fadeInOpacity;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}
@-webkit-keyframes fadeInOpacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeInOpacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.homepage--landing {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
    height: 100vh;
}
.homepage--landing--texts {
      opacity: 0;
      margin-left: 10vw;
}
.homepage--landing--texts .name, .homepage--landing--texts .description {
        font-size: 2rem;
        color: #deeef5;
}
.homepage--landing--texts .name {
        padding-top: 2rem;
        margin-bottom: 30vh;
}
.homepage--landing--texts .description {
        padding-top: 1rem;
        padding-right: 1rem;
}
.homepage--landing--texts .position-title {
        font-size: calc(3rem + 1vw);
        padding-right: 1rem;
}
.homepage--landing--texts .position-title:after {
          content: ' ';
          display: block;
          width: 50px;
          border: 2px solid #fff;
}
.homepage--about {
    color: #258def;
    height: 100%;
    margin-top: 1px;
}
.homepage--about--texts {
      opacity: 0;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.homepage--about .about {
      color: #5d748c;
      font-size: 1.5rem;
      padding-right: 2rem;
      padding-bottom: 2rem;
}
.homepage--about .about-text1 {
      margin-bottom: 3rem;
}
.homepage--about .about-text1, .homepage--about .about-text2 {
      padding-right: 4rem;
}
.homepage--experience {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage--experience--texts {
      opacity: 0;
}
.homepage--experience .work-exp:nth-child(n+2) {
      margin-top: 2rem;
}
.homepage--experience .link {
      display: inline-block;
      padding: 0 .5rem;
      font-size: 1.5rem;
}
.homepage--experience .company {
      display: inline-block;
      font-size: 2rem;
}
.homepage--experience .position, .homepage--experience .duration {
      color: #deeef5;
}
.homepage--skillset--texts {
    opacity: 0;
}
.homepage--skillset .desc {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.homepage--skillset .skill {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: 1.5rem;
    border: 2px solid;
    width: 125px;
    padding: .5rem;
    margin: .5rem;
    border-radius: 25px;
}
.homepage--education {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage--education--texts {
      opacity: 0;
}
.homepage--education .education-desc {
      padding-bottom: 1rem;
}
.homepage--education .year {
      color: #deeef5;
}
.homepage--social-media--texts {
    opacity: 0;
}
.homepage--social-media .social-icon {
    font-size: 3rem;
    margin-right: 1rem;
    padding: .5rem;
}
.homepage--social-media .desc {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.homepage--social-media .title .title-icon.contact-icon {
    margin-left: 0;
}
.homepage .hp-section {
    width: 100vw;
}
@media (max-width: 750px) {
.homepage .text-section {
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.homepage .title {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.homepage .skill {
      font-size: 1rem;
      width: 80px;
}
.homepage .desc {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      text-align: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      padding-top: 2rem;
}
.homepage .desc.about {
        padding: 0;
}
.homepage .desc .about-text1, .homepage .desc .about-text2 {
        padding: 0;
}
.homepage .work-exp {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      text-align: center;
}
.homepage .skills {
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
          -ms-flex-direction: row;
              flex-direction: row;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.homepage .year, .homepage .degree, .homepage .university {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
}
.homepage .section-name {
      text-align: center;
}
.homepage .section-name::after {
        width: 100% !important;
}
.homepage .social-icon {
      margin: 0;
}
}
.homepage .text-section {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding-left: 3vw;
    padding-top: 3rem;
    padding-bottom: 3rem;
}
.homepage .text-section .title {
      width: 35vw;
      letter-spacing: 2px;
}
.homepage .text-section .title .section-name {
        font-size: 2rem;
}
.homepage .text-section .title .section-name:after {
          content: ' ';
          display: block;
          width: 50px;
          border: 2px solid;
          margin-top: .5rem;
}
.homepage .text-section .title-icon {
      margin-top: 1rem;
      margin-left: -.5rem;
      font-size: 4rem;
}
.homepage .text-section .desc {
      width: 65vw;
}
